import { Dispatch, useCallback, useLayoutEffect, useMemo, useState } from "react"
import { SetStateAction } from "jotai"
import { Watchlist } from "@rp/common/api-types"
import { HEADER_HEIGHT, WATCHLIST_ENTITIES_FEATURE_NAME } from "@rp/common/settings"
import { formatNumber, isDefined } from "@rp/common/utils"
import { useIsWatchlistEntityLimitMet } from "@rp/react/common/hooks"
import { useAccount } from "@rp/react/common/providers"
import { useCqsByIdsContext } from "@rp/react/tanstack-api/cqs-by-ids"
import {
  PanelConfig,
  WatchlistGroups,
  usePanelConfig,
} from "@rp/react/tanstack-api/discovery-panel-config"
import {
  GenericWatchlistAutosuggestPostParams,
  WatchlistAutosuggestPostResponse,
  WatchlistSuggestionFilters,
  convertSuggestionToCqsItem,
  getEmptySuggestion,
  isEnhancedCqsItemSuggestion,
  isEntitySuggestion,
} from "@rp/react/tanstack-api/suggestion"
import { useElementSize } from "@rp/react/web/common/hooks"
import { useModal } from "@rp/react/web/common/providers"
import {
  Box,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Typography,
} from "@rp/react/web/mui/core"
import { AddItemsButton } from "./AddItemsButton"
import { SuggestionsFilters } from "./SuggestionFilters/SuggestionsFilters"
import { WatchlistEmptyPlaceholder } from "./WatchlistEmptyPlaceholder"
import { WatchlistSuggestionItem } from "./WatchlistSuggestionItem"

type Props = {
  pages: Array<WatchlistAutosuggestPostResponse>
  included: Array<string>
  isLoading: boolean
  onAddSuggestions: (ids: Array<string>) => void
  categoryFilter?: string
  isPatching?: boolean
  filters: WatchlistSuggestionFilters
  perPage?: number
  setPerPage?: (perPage: number) => void
  page?: number
  setPage?: Dispatch<SetStateAction<number>>
  setFilters?: Dispatch<SetStateAction<WatchlistSuggestionFilters>>
  fetchNextPage?: () => Promise<unknown>
  suggestionQuery: GenericWatchlistAutosuggestPostParams
  watchlist: Watchlist
  listContainer: HTMLDivElement | null
}

const getEmptySuggestions = (perPage: number) => {
  return new Array(perPage)
    .fill(0)
    .map((_, index) => index.toString())
    .map(getEmptySuggestion)
}

const getDisplayCategories = (categories: PanelConfig["discoveryPanelCategories"]) => {
  return categories.reduce<Record<string, WatchlistGroups>>((acc, item) => {
    return { ...acc, [item.label]: item.watchlistGroups }
  }, {})
}

const WatchlistSuggestionList = ({
  pages,
  included,
  isLoading,
  onAddSuggestions,
  categoryFilter,
  isPatching,
  filters,
  setFilters,
  fetchNextPage,
  setPerPage,
  perPage = 0,
  page = 0,
  setPage,
  suggestionQuery,
  watchlist,
  listContainer,
}: Props) => {
  const facets = pages[0]?.facets ?? {}
  const { addByIds } = useCqsByIdsContext()
  const { width } = useElementSize(listContainer)
  const showFiltersNewLine = width < 900 && categoryFilter

  const MENU_HEIGHT = showFiltersNewLine ? 110 : 58
  const suggestions = useMemo(
    () => (pages[page]?.results ?? []).filter(isEnhancedCqsItemSuggestion),
    [page, pages],
  )
  const [selectedSuggestions, setSelectedSuggestions] = useState<Array<string>>([])
  const [multiSelectState, setMultiSelectState] = useState<"checked" | "unchecked" | "disabled">(
    "unchecked",
  )
  const addSelectedSuggestion = useCallback((key: string) => {
    setSelectedSuggestions(prev => [...prev, key])
  }, [])
  const { data: panelConfig } = usePanelConfig()
  const [categoryFields, categoryTitle] = useMemo(() => {
    const categoryMap = getDisplayCategories(panelConfig?.discoveryPanelCategories ?? [])
    return categoryFilter
      ? [categoryMap[categoryFilter].fields, categoryMap[categoryFilter].title]
      : [[], undefined]
  }, [categoryFilter, panelConfig])

  const removeSelectedSuggestion = useCallback((key: string) => {
    setSelectedSuggestions(prev => {
      const filteredSuggestions = prev.filter(selectedKey => selectedKey !== key)
      return filteredSuggestions
    })
  }, [])

  const addAll = useCallback(() => {
    const suggestionsIds = suggestions
      .map(suggestion => suggestion.key)
      .filter(suggestionId => !included.includes(suggestionId))
    const allSuggestions = new Set([...selectedSuggestions, ...suggestionsIds])
    setSelectedSuggestions([...allSuggestions])
  }, [included, selectedSuggestions, suggestions])

  const removeAll = useCallback(() => {
    const suggestionsIds = suggestions.map(suggestion => suggestion.key)
    setSelectedSuggestions(
      selectedSuggestions.filter(suggestionId => !suggestionsIds.includes(suggestionId)),
    )
  }, [selectedSuggestions, suggestions])

  const numOfResults = pages[0]?.count ?? 0
  const {
    planComparisonModal: { handleOpen: planComparisonHandleOpen },
  } = useModal()
  const { quota } = useAccount()
  const isWatchlistEntityLimitMet = useIsWatchlistEntityLimitMet(
    quota,
    included.length + selectedSuggestions.length,
  )
  // When suggestions change, set state of top checkbox
  useLayoutEffect(() => {
    if (
      suggestions.length <= included.length &&
      suggestions.every(suggestion => included.includes(suggestion.key))
    ) {
      setMultiSelectState("disabled")
    } else if (
      suggestions.length <= selectedSuggestions.length + included.length &&
      suggestions.every(
        suggestion =>
          selectedSuggestions.includes(suggestion.key) || included.includes(suggestion.key),
      )
    ) {
      setMultiSelectState("checked")
    } else {
      setMultiSelectState("unchecked")
    }
  }, [suggestions, removeAll, selectedSuggestions, included])

  const hasResults = !(!isLoading && suggestions.length === 0)

  const tableLayout = categoryFilter && categoryFilter !== "Concepts" ? "fixed" : undefined
  return (
    <Box sx={{ width: "100%", height: "100%" }}>
      <Box
        sx={{
          position: "sticky",
          height: MENU_HEIGHT,
          top: HEADER_HEIGHT,
          paddingRight: "24px",
          justifyContent: "space-between",
          alignItems: "end",
          width: "calc(100% + 24px)",
          backgroundColor: "surface.main",
          pt: 3,
          pl: 0,
          zIndex: 9,
        }}
      >
        <Box sx={{ display: "flex", width: "100%", alignItems: "center", minHeight: 33 }}>
          <Typography color="grey.300" variant="caption" sx={{ flexGrow: 1, whiteSpace: "nowrap" }}>
            {isLoading
              ? ""
              : `Showing ${formatNumber(numOfResults)} item${numOfResults > 1 ? "s" : ""}`}
          </Typography>
          {!showFiltersNewLine ? (
            <SuggestionsFilters
              isLoading={isLoading}
              facets={facets}
              maxWidth={width - 400}
              categoryFilter={categoryFilter}
              filters={filters}
              setFilters={setFilters}
              suggestionQuery={suggestionQuery}
              fields={categoryFields}
            />
          ) : null}
          <AddItemsButton
            watchlistItemsCount={watchlist.items.length}
            amount={selectedSuggestions.length}
            onClick={() => {
              if (isWatchlistEntityLimitMet) {
                planComparisonHandleOpen(WATCHLIST_ENTITIES_FEATURE_NAME)
                return
              }
              const ids = selectedSuggestions
                .map(key => suggestions.find(i => i.key === key))
                .filter(isDefined)
              const toAdd = ids
                .map(item =>
                  isEntitySuggestion(item) ? convertSuggestionToCqsItem(item) : undefined,
                )
                .filter(isDefined)
              addByIds(toAdd)
              setSelectedSuggestions([])
              onAddSuggestions(selectedSuggestions)
            }}
            loading={isPatching}
          />
        </Box>
        {showFiltersNewLine ? (
          <Box
            sx={{
              flexWrap: "nowrap",
              my: 2,
              display: "flex",
              alignItems: "center",
              justifyContent: "left",
              flexGrow: 1,
            }}
          >
            <SuggestionsFilters
              isLoading={isLoading}
              facets={facets}
              clearText="Clear"
              maxWidth={width - 70}
              categoryFilter={categoryFilter}
              filters={filters}
              setFilters={setFilters}
              suggestionQuery={suggestionQuery}
              fields={categoryFields}
            />
          </Box>
        ) : null}
      </Box>
      {!hasResults ? (
        <Box sx={{ display: "flex", alignItems: "center", height: "calc(100% - 58px)" }}>
          <WatchlistEmptyPlaceholder
            placeholder={
              "We couldn't find any results that match your search criteria. Please try again with different keywords."
            }
          />
        </Box>
      ) : null}

      {hasResults ? (
        <Box>
          <Table sx={{ width: "100%", tableLayout }}>
            <TableHead
              sx={theme => ({
                position: "sticky",
                top: MENU_HEIGHT + HEADER_HEIGHT,
                backgroundColor: "surface.main",
                zIndex: 9,
                boxShadow: `0px 1px ${theme.palette.outline.border}`,
                height: 48,
                "& th": {
                  fontWeight: 700,
                  fontSize: 12,
                  py: 0,
                },
              })}
            >
              <TableRow>
                <TableCell sx={{ width: "60px" }}>
                  <Checkbox
                    disabled={multiSelectState === "disabled" || isLoading || isPatching}
                    checked={
                      multiSelectState === "checked" ||
                      (multiSelectState === "disabled" && !isLoading)
                    }
                    onChange={(_, value) => (value ? addAll() : removeAll())}
                  />
                </TableCell>
                {categoryFilter ? (
                  <>
                    <TableCell>{categoryTitle}</TableCell>
                    {categoryFields.map(({ label }) => (
                      <TableCell key={label}>{label}</TableCell>
                    ))}
                  </>
                ) : (
                  <>
                    <TableCell>NAME</TableCell>
                    <TableCell>TYPE</TableCell>
                  </>
                )}
              </TableRow>
            </TableHead>
            <TableBody>
              {(isLoading ? getEmptySuggestions(perPage) : suggestions).map(suggestion => (
                <WatchlistSuggestionItem
                  isLoading={isLoading}
                  key={suggestion.key}
                  suggestion={suggestion}
                  selected={
                    included.includes(suggestion.key) ||
                    selectedSuggestions.includes(suggestion.key)
                  }
                  disabled={isPatching}
                  included={included.includes(suggestion.key)}
                  onChange={(key, value) =>
                    value ? addSelectedSuggestion(key) : removeSelectedSuggestion(key)
                  }
                  categoryFilter={categoryFilter}
                  fields={categoryFields}
                />
              ))}
            </TableBody>
          </Table>
          {numOfResults > perPage ? (
            <TablePagination
              component="div"
              count={numOfResults}
              labelDisplayedRows={({ from, to, count }) => {
                return (
                  <>
                    {from}â€“{to} of {formatNumber(count)}
                  </>
                )
              }}
              page={page}
              onPageChange={(ev, newPage) => {
                fetchNextPage?.().then(() => {
                  setPage?.(newPage)
                  setTimeout(() => {
                    window.scrollTo({ top: 0, behavior: "smooth" })
                  }, 100)
                })
                // window.scrollTo({ top: 0, behavior: "smooth" })
              }}
              rowsPerPage={perPage}
              onRowsPerPageChange={ev => {
                setPerPage?.(Number(ev.target.value))
                setTimeout(() => {
                  window.scrollTo({ top: 0, behavior: "smooth" })
                }, 100)
              }}
            />
          ) : null}
        </Box>
      ) : null}
    </Box>
  )
}

export { WatchlistSuggestionList, getDisplayCategories }
